































































import { Component, Watch } from 'vue-property-decorator'
import BaseView from '@/mixins/BaseView.vue'
import LocalStorageStore from '@/store/modules/LocalStorageStore'
import { debounce } from 'lodash-es'

@Component
export default class Login extends BaseView {
  public alert = false

  public valid = true

  public email = ''

  public password = ''

  public notEmptyRules: Array<Function> = [(v: string) => !!v || '']

  public passwordRules = [
    (v: string) => !!v || '',
    (v: string) => v.length <= 32 || '32',
    (v: string) => v.length >= 8 || '8'
  ]

  public errorMsg: {
    email: string
  } = {
    email: ''
  }

  public debouncedCheckEmail: Function

  $refs: {
    form: HTMLFormElement
  }

  public async login () {
    this.$refs.form.validate()
    try {
      const response = await this.$axios
        .post('login', {
          email: this.email,
          password: this.password
        })
      if (response.data.message === '') {
        this.messageSuccess(response.data.message)
        LocalStorageStore.setToken('token ' + response.data.token)
        LocalStorageStore.setEmail(this.email)
        await this.$router.replace('/division/1')
        // await this.preload()
      } else {
        this.valid = false
        this.messageError(response.data.message)
      }
    } catch (e) {
      this.valid = false
      this.messageError('')
    }
  }

  public checkEmail (): void {
    if (!/^[0-9]{11}@(m\.)?fudan\.edu\.cn$/.test(this.email)) {
      this.errorMsg.email = '@fudan.edu.cn'
    } else {
      this.errorMsg.email = ''
    }
  }

  @Watch('email')
  emailChanged () {
    this.debouncedCheckEmail()
  }

  created () {
    this.debouncedCheckEmail = debounce(this.checkEmail, 500)
  }
}
